// components/ShopItemCard.tsx
"use client";

import React from "react";
import Image from "next/image";

type ShopItemCardProps = {
  item: any;
  userPoints: number;
  onBuy: (itemId: number) => Promise<boolean> | boolean;
};

const ShopItemCard: React.FC<ShopItemCardProps> = ({
  item,
  userPoints,
  onBuy,
}) => {
  const notEnough = userPoints < item.cost;

  const handleClick = async () => {
    await onBuy(item.id); // backend-ul tƒÉu rƒÉm√¢ne la fel
  };

  return (
    // cardul tƒÉu GRI devine flex + h-full
    <div className="bg-[#5b6470] rounded-[32px] shadow-[0_18px_0_rgba(0,0,0,0.55)] border border-[#7b8490]/60 px-8 py-6 flex flex-col items-center h-full">
      {/* IMAGINEA ‚Äì exact cum o aveai */}
      <div className="mt-2 mb-6">
        <Image
          src={item.image} // cum √Æl ai tu: /images/whatever.png
          alt={item.name}
          width={160}
          height={160}
          className="object-contain"
        />
      </div>

      {/* TITLU + DESCRIERE ‚Äì exact stilul tƒÉu */}
      <h2 className="text-2xl font-extrabold text-green-800">{item.name}</h2>
      <p className="mt-2 text-green-700">{item.description}</p>

      {/* COST / DIAMANT */}
      <div className="mt-4 flex items-center gap-2 text-green-800 font-semibold">
        <span className="text-sky-300 text-xl">üíé</span>
        <span>{item.cost}</span>
      </div>

      {/* SPA»öIU CARE √éMPINGE BUTONUL JOS */}
      <div className="mt-auto w-full">
        <button
          onClick={handleClick}
          disabled={notEnough}
          className={`
            mt-6 w-full py-3 rounded-full font-bold text-white text-sm
            ${
              notEnough
                ? "bg-[#c3c7d0] text-[#5b6470] cursor-not-allowed"
                : "bg-[#b5bec8] hover:bg-[#c3ccd6]"
            }
          `}
        >
          {notEnough ? "Not enough points" : "Redeem"}
        </button>
      </div>
    </div>
  );
};

export default ShopItemCard;
